'use client';

import { Booking } from '@/src/lib/booking/BookingTypes';
import { format } from 'date-fns';
import { Button } from '@/src/components/ui/button';

interface Props {
  bookings: Booking[];
  onEdit: (booking: Booking) => void;
  onDelete: (id: string) => void;
}

export default function BookingTable({ bookings, onEdit, onDelete }: Props) {
  return (
    <div className="overflow-x-auto mt-4">
      <table className="min-w-full text-sm border">
        <thead className="bg-gray-100">
          <tr>
            <th className="border p-2">Full Name</th>
            <th className="border p-2">Phone</th>
            <th className="border p-2">Vehicle Model</th>
            <th className="border p-2">Start Date</th>
            <th className="border p-2">End Date</th>
            <th className="border p-2">Actions</th>
          </tr>
        </thead>
        <tbody>
          {bookings.map((b) => (
            <tr key={b.id}>
              <td className="border p-2">{b.fullName}</td>
              <td className="border p-2">{b.phone}</td>
              <td className="border p-2">{b.vehicleModel}</td>
              <td className="border p-2">
                {b.rentalStartDate ? format(b.rentalStartDate.toDate(), 'dd/MM/yyyy') : '-'}
              </td>
              <td className="border p-2">
                {b.rentalEndDate ? format(b.rentalEndDate.toDate(), 'dd/MM/yyyy') : '-'}
              </td>

              <td className="border p-2 flex gap-2">
                <Button  onClick={() => onEdit(b)}>Edit</Button>
                <Button variant="destructive" onClick={() => onDelete(b.id)}>Delete</Button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
