'use client';

import { useEffect, useState } from 'react';
import { Booking } from '@/src/lib/booking/BookingTypes';
import { collection, getDocs, addDoc, updateDoc, deleteDoc, doc, serverTimestamp, Timestamp } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';

export function useBookingData() {
  const [bookings, setBookings] = useState<Booking[]>([]);
  const [editingBooking, setEditingBooking] = useState<Booking | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchBookings();
  }, []);

  const fetchBookings = async () => {
    setLoading(true);
    try {
      const snap = await getDocs(collection(db, 'bookings'));
      const list: Booking[] = snap.docs.map(docSnap => ({
        id: docSnap.id,
        ...(docSnap.data() as Omit<Booking, 'id'>),
      }));
      setBookings(list);
    } catch (error) {
      console.error('Error fetching bookings:', error);
    } finally {
      setLoading(false);
    }
  };

  const saveBooking = async (data: Omit<Booking, 'id' | 'createdAt' | 'updatedAt'>) => {
    try {
      if (editingBooking) {
        const bookingRef = doc(db, 'bookings', editingBooking.id);
        await updateDoc(bookingRef, {
          ...data,
          updatedAt: serverTimestamp(),
        });
        setBookings((prev) =>
          prev.map((b) =>
            b.id === editingBooking.id
              ? { ...b, ...data, updatedAt: Timestamp.now() }
              : b
          )
        );
        setEditingBooking(null);
      } else {
        const newDoc = await addDoc(collection(db, 'bookings'), {
          ...data,
          createdAt: serverTimestamp(),
          updatedAt: serverTimestamp(),
        });

        // üëâ Khi update state local:
        setBookings((prev) => [
          ...prev,
          {
            id: newDoc.id,
            ...data,
            createdAt: Timestamp.now(),
            updatedAt: Timestamp.now(),
          },
        ]);
      }
    } catch (error) {
      console.error('Error saving booking:', error);
    }
  };

  const deleteBooking = async (id: string) => {
    try {
      await deleteDoc(doc(db, 'bookings', id));
      setBookings((prev) => prev.filter((b) => b.id !== id));
    } catch (error) {
      console.error('Error deleting booking:', error);
    }
  };

  return {
    bookings,
    editingBooking,
    setEditingBooking,
    saveBooking,
    deleteBooking,
    loading,
  };
}
